
import { defineComponent, computed } from 'vue';

export default defineComponent({
  name: 'arrowIcon',
  props: {
    direction: {
      type: String,
      default: 'right',
    },
  },
  setup(props) {
    const iconDirectionClass = computed(() => `vms-icon-${props.direction}`);

    return {
      iconDirectionClass,
    };
  },
});
